import React, { Component } from 'react'
import axios from 'axios'

export default class ContactForm extends Component {
  state = {
    formFields: {
      name: '',
      email: '',
      message: '',
    },
    formSubmissionStatus: null,
  }

  inputChangeHandler = (e, propkey) => {
    const newState = {
      ...this.state,
      formFields: { ...this.state.formFields, [propkey]: e.target.value },
    }
    this.setState(newState)
  }

  submitHandler = e => {
    console.log(this.state)
    axios
      .post('https://formcarry.com/s/r7E6uw9_6LR', this.state.formFields, {
        headers: { Accept: 'application/json' },
      })
      .then(res => {
        this.setState({
          formSubmissionStatus: res,
          formFields: { name: '', email: '', message: '' },
        })
      })
      .catch(error => {
        console.log(error)
      })
    e.preventDefault()
  }

  render() {
    return (
      <div className="row">
        <div className="8u 12u$(small)">
          <form onSubmit={this.submitHandler} /* method="post" action="#" */>
            <div className="row uniform 50%">
              <div className="6u 12u$(xsmall)">
                <input
                  type="text"
                  name="name"
                  id="name"
                  placeholder="Name"
                  onChange={event => this.inputChangeHandler(event, 'name')}
                />
              </div>
              <div className="6u 12u$(xsmall)">
                <input
                  type="email"
                  name="email"
                  id="email"
                  placeholder="Email"
                  onChange={event => this.inputChangeHandler(event, 'email')}
                />
              </div>
              <div className="12u">
                <textarea
                  name="message"
                  id="message"
                  placeholder="Message"
                  rows="4"
                  onChange={event => this.inputChangeHandler(event, 'message')}
                />
              </div>
            </div>
            <ul className="actions" style={{ marginTop: '1rem' }}>
              <li>
                <input type="submit" value="Send Message" />
              </li>
            </ul>
          </form>
          {this.state.formSubmissionStatus &&
            this.state.formSubmissionStatus.status === 200 && (
              <p>Thank You. Your message has been sent.</p>
            )}
        </div>
        <div className="4u 12u$(small)">
          <ul className="labeled-icons">
            <li>
              <h3 className="icon fa-home">
                <span className="label">Address</span>
              </h3>
              171 Prospect rd.
              <br />
              Walmer, PE
              <br />
              Eastern Cape, 6070
            </li>
            <li>
              <h3 className="icon fa-mobile">
                <span className="label">Phone</span>
              </h3>
              072-266-9790
            </li>
            {/* <li>
                        <h3 className="icon fa-envelope-o"><span className="label">Email</span></h3>
                        <a href="#">hello@untitled.tld</a>
                    </li> */}
          </ul>
        </div>
      </div>
    )
  }
}
